@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;900&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 12px;
  padding: 0px;
}

.main-container {
  margin: 0px;
  padding: 0px;
}

.header {
  color: white;
  text-align: center;
  background-color: rgb(93, 188, 210);
}

.header-part1 {
  font-weight: 900;
  font-size: 36px;
}

.header-part2 {
  font-weight: 900;
  font-size: 20px;
}

.disclaimer-strip {
  color: black;
  text-align: center;
  background-color: rgb(247, 247, 247);
  font-size: 16px;
  margin-top: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-style: solid;
  border-width: 1px;
  border-color: rgb(220, 220, 220);
}

.disclaimer-text {
  color: black;
  text-align: left;
  background-color: rgb(247, 247, 247);
  font-size: 16px;
  border-style: solid;
  border-width: 1px;
  border-color: rgb(220, 220, 220);
}

.linkClass {
  font-weight: normal;
}

.inputs {
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.inputForm-header {
  font-weight: bold;
  flex-direction: row-reverse;
}

.button-send {
  text-align: center;
}

.resultsCard {
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.borders {
  border-style: solid;
  border-width: 1px;
  border-color: black;
}

.form {
  padding-left: 10px;
  padding-right: 10px;
}

.results {
  margin-top: 10px;
  margin-bottom: 30px;
}

.table-cell-center {
  text-align: center;
}

.table-cell-bold {
  font-weight: 900;
}

.custom-control-label:before {
  background-color: red;
}
.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
  background-color: black;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.resultsTableDescription {
  margin-bottom: 0px;
}

